.collection-container-snax{
    position: relative;
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    height: 360px;
    border-radius: 24px;
    background: linear-gradient(153deg, #12a1de 0%, #c6ffd9 100%);
    margin-bottom: 24px;
}
.popcorn-container{
    background: linear-gradient(137deg, #de4f12 12.37%, #f7d385 100%);
}
.collection-text-container-snax{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 550px;
    // margin-right: 120px;
    
    padding-top: 65px;
    padding-bottom: 65px;
}
.text-snax{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 48px;
}
